$box-side: 100px;
$gap: 1px;
$days-in-week: 7;
$calendar-padding: 10px;
$box-with-gap: $box-side + $gap;

$days-of-the-week:
  "mon" 0,
  "tue" ($box-with-gap),
  "wed" ($box-with-gap * 2),
  "thu" ($box-with-gap * 3),
  "fri" ($box-with-gap * 4),
  "sat" ($box-with-gap * 5),
  "sun" ($box-with-gap * 6);
